"use client"

import { useState, useEffect } from 'react'
import { Consumo, Trabajador, Proyecto, EPP } from '@/types'

interface ConsumoItem {
  eppId: number
  codigo: string
  descripcion: string
  cantidadConsumida: number
  observacion?: string
  epp?: EPP
}

export default function ConsumosPage() {
  const [consumos, setConsumos] = useState<Consumo[]>([])
  const [trabajadores, setTrabajadores] = useState<Trabajador[]>([])
  const [proyectos, setProyectos] = useState<Proyecto[]>([])
  const [epp, setEpp] = useState<EPP[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  
  // New state for the updated form
  const [rutInput, setRutInput] = useState('')
  const [selectedTrabajador, setSelectedTrabajador] = useState<Trabajador | null>(null)
  const [proyectoId, setProyectoId] = useState('')
  const [maquina, setMaquina] = useState('')
  const [centroCosto, setCentroCosto] = useState('')
  const [codigoInput, setCodigoInput] = useState('')
  const [cantidadInput, setCantidadInput] = useState('')
  const [observacionInput, setObservacionInput] = useState('')
  const [consumoItems, setConsumoItems] = useState<ConsumoItem[]>([])
  const [showPrintPreview, setShowPrintPreview] = useState(false)
  const [searchError, setSearchError] = useState<string | null>(null)

  useEffect(() => {
    loadData()
  }, [])

  const loadData = async () => {
    setLoading(true)
    setError(null)
    
    try {
      const [consumosRes, trabajadoresRes, proyectosRes, eppRes] = await Promise.all([
        fetch('/api/consumos'),
        fetch('/api/trabajadores'),
        fetch('/api/proyectos'),
        fetch('/api/epp')
      ])

      const [consumosData, trabajadoresData, proyectosData, eppData] = await Promise.all([
        consumosRes.json(),
        trabajadoresRes.json(),
        proyectosRes.json(),
        eppRes.json()
      ])
      
      // Handle API response format: { success: true, data: [...] }
      const consumosArray = consumosData.data || consumosData
      const trabajadoresArray = trabajadoresData.data || trabajadoresData
      const proyectosArray = proyectosData.data || proyectosData
      const eppArray = eppData.data || eppData
      
      // Ensure all are arrays
      setConsumos(Array.isArray(consumosArray) ? consumosArray : [])
      setTrabajadores(Array.isArray(trabajadoresArray) ? trabajadoresArray : [])
      setProyectos(Array.isArray(proyectosArray) ? proyectosArray : [])
      setEpp(Array.isArray(eppArray) ? eppArray : [])
    } catch (error) {
      console.error('Error loading data:', error)
      setError('Error al cargar los datos. Por favor, intente nuevamente.')
      
      // Set empty arrays on error
      setConsumos([])
      setTrabajadores([])
      setProyectos([])
      setEpp([])
    } finally {
      setLoading(false)
    }
  }

  const handleBuscarTrabajador = () => {
    setSearchError(null)
    if (!rutInput.trim()) {
      setSearchError('Por favor ingrese un RUT')
      return
    }

    const trabajador = trabajadores.find(t => 
      t.rut.toLowerCase().replace(/[.-]/g, '') === rutInput.toLowerCase().replace(/[.-]/g, '')
    )

    if (trabajador) {
      setSelectedTrabajador(trabajador)
      setSearchError(null)
    } else {
      setSelectedTrabajador(null)
      setSearchError('Trabajador no encontrado')
    }
  }

  const handleAgregarCodigo = () => {
    if (!codigoInput.trim() || !cantidadInput.trim()) {
      setError('Por favor complete el código y la cantidad')
      return
    }

    const eppItem = epp.find(e => e.codigo.toLowerCase() === codigoInput.toLowerCase())
    
    if (!eppItem) {
      setError('Código de producto no encontrado')
      return
    }

    const cantidad = parseInt(cantidadInput)
    if (cantidad <= 0) {
      setError('La cantidad debe ser mayor a 0')
      return
    }

    if (eppItem.cantidadActual < cantidad) {
      setError(`Stock insuficiente. Disponible: ${eppItem.cantidadActual}`)
      return
    }

    // Check if item already exists in the list
    const existingItemIndex = consumoItems.findIndex(item => item.eppId === eppItem.id)
    
    if (existingItemIndex >= 0) {
      // Update existing item
      const updatedItems = [...consumoItems]
      const totalCantidad = updatedItems[existingItemIndex].cantidadConsumida + cantidad
      
      if (eppItem.cantidadActual < totalCantidad) {
        setError(`Stock insuficiente para cantidad total: ${totalCantidad}. Disponible: ${eppItem.cantidadActual}`)
        return
      }
      
      updatedItems[existingItemIndex].cantidadConsumida = totalCantidad
      updatedItems[existingItemIndex].observacion = observacionInput || updatedItems[existingItemIndex].observacion
      setConsumoItems(updatedItems)
    } else {
      // Add new item
      const newItem: ConsumoItem = {
        eppId: eppItem.id,
        codigo: eppItem.codigo,
        descripcion: eppItem.descripcion,
        cantidadConsumida: cantidad,
        observacion: observacionInput,
        epp: eppItem
      }
      setConsumoItems([...consumoItems, newItem])
    }

    // Clear inputs
    setCodigoInput('')
    setCantidadInput('')
    setObservacionInput('')
    setError(null)
  }

  const handleRemoveItem = (index: number) => {
    const updatedItems = consumoItems.filter((_, i) => i !== index)
    setConsumoItems(updatedItems)
  }

  const handleConfirmarConsumo = async () => {
    if (!selectedTrabajador || !proyectoId || consumoItems.length === 0) {
      setError('Por favor complete todos los campos requeridos')
      return
    }

    try {
      const payload = {
        trabajadorId: selectedTrabajador.id,
        proyectoId: parseInt(proyectoId),
        maquina,
        centroCosto,
        items: consumoItems.map(item => ({
          eppId: item.eppId,
          cantidadConsumida: item.cantidadConsumida,
          observacion: item.observacion
        }))
      }

      const response = await fetch('/api/consumos', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(payload)
      })

      if (!response.ok) {
        const errorData = await response.json()
        throw new Error(errorData.error || 'Error al registrar el consumo')
      }

      // Reset form
      setRutInput('')
      setSelectedTrabajador(null)
      setProyectoId('')
      setMaquina('')
      setCentroCosto('')
      setConsumoItems([])
      setShowPrintPreview(false)
      setError(null)
      
      // Reload data
      loadData()
      
      // Trigger print
      window.print()
      
    } catch (error) {
      console.error('Error confirming consumo:', error)
      setError(error instanceof Error ? error.message : 'Error al confirmar el consumo')
    }
  }

  const handleDelete = async (id: number) => {
    if (!confirm('¿Está seguro de eliminar este consumo?')) return
    
    try {
      await fetch(`/api/consumos?id=${id}`, { method: 'DELETE' })
      loadData()
    } catch (error) {
      console.error('Error deleting consumo:', error)
      setError('Error al eliminar el consumo. Por favor, intente nuevamente.')
    }
  }

  const selectedProyecto = proyectos.find(p => p.id === parseInt(proyectoId))

  if (loading) {
    return (
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="bg-white shadow-md rounded-lg p-6">
          <h1 className="text-2xl font-bold mb-6 text-slate-800">Registro de Consumos</h1>
          <div className="flex justify-center items-center h-64">
            <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-orange-500"></div>
          </div>
        </div>
      </div>
    )
  }

  return (
    <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 space-y-6">
      {/* Print Preview Button at Top */}
      <div className="bg-white shadow-md rounded-lg p-4">
        <div className="flex justify-between items-center">
          <h1 className="text-2xl font-bold text-slate-800">Registro de Consumos</h1>
          <button
            onClick={() => setShowPrintPreview(true)}
            disabled={!selectedTrabajador || !proyectoId || consumoItems.length === 0}
            className="bg-blue-600 text-white px-6 py-2 rounded-lg hover:bg-blue-700 disabled:bg-gray-400 disabled:cursor-not-allowed transition-colors"
          >
            Vista Previa de Impresión
          </button>
        </div>
      </div>

      {/* Error Display */}
      {error && (
        <div className="bg-red-50 border border-red-200 rounded-md p-4">
          <p className="text-red-800">{error}</p>
          <button 
            onClick={() => setError(null)}
            className="mt-2 text-red-600 hover:text-red-800 text-sm underline"
          >
            Cerrar
          </button>
        </div>
      )}

      {/* Worker Search Section */}
      <div className="bg-white shadow-md rounded-lg p-6">
        <h2 className="text-lg font-semibold mb-4 text-slate-800">1. Información del Trabajador</h2>
        <div className="space-y-4">
          <div>
            <label className="block text-sm font-medium text-slate-700 mb-2">RUT del Trabajador</label>
            <div className="flex items-center space-x-2">
              <input
                type="text"
                placeholder="Ingrese el RUT (ej: 12345678-9)"
                value={rutInput}
                onChange={(e) => setRutInput(e.target.value)}
                className="border border-slate-300 rounded-lg px-3 py-2 flex-1 focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                onKeyPress={(e) => e.key === 'Enter' && handleBuscarTrabajador()}
              />
              <button
                onClick={handleBuscarTrabajador}
                className="bg-blue-600 text-white px-4 py-2 rounded-lg hover:bg-blue-700 transition-colors"
              >
                Buscar
              </button>
            </div>
            {searchError && (
              <p className="text-red-600 text-sm mt-1">{searchError}</p>
            )}
          </div>

          {/* Worker Details Display */}
          {selectedTrabajador && (
            <div className="bg-slate-50 border border-slate-200 rounded-lg p-4">
              <h3 className="font-medium text-slate-800 mb-2">Datos del Trabajador:</h3>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4 text-sm">
                <div><span className="font-medium">Nombre:</span> {selectedTrabajador.nombre}</div>
                <div><span className="font-medium">RUT:</span> {selectedTrabajador.rut}</div>
                <div><span className="font-medium">Cargo:</span> {selectedTrabajador.cargo}</div>
                <div><span className="font-medium">Email:</span> {selectedTrabajador.email || 'N/A'}</div>
              </div>
            </div>
          )}
        </div>
      </div>

      {/* Project and Additional Info Section */}
      <div className="bg-white shadow-md rounded-lg p-6">
        <h2 className="text-lg font-semibold mb-4 text-slate-800">2. Información del Proyecto</h2>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <div>
            <label className="block text-sm font-medium text-slate-700 mb-2">Proyecto *</label>
            <select
              value={proyectoId}
              onChange={(e) => setProyectoId(e.target.value)}
              className="w-full border border-slate-300 rounded-lg px-3 py-2 focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
              required
            >
              <option value="">Seleccionar proyecto</option>
              {proyectos.map((p) => (
                <option key={p.id} value={p.id}>{p.nombreProyecto}</option>
              ))}
            </select>
          </div>
          <div>
            <label className="block text-sm font-medium text-slate-700 mb-2">Máquina</label>
            <input
              type="text"
              placeholder="Nombre de la máquina"
              value={maquina}
              onChange={(e) => setMaquina(e.target.value)}
              className="w-full border border-slate-300 rounded-lg px-3 py-2 focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
            />
          </div>
          <div className="md:col-span-2">
            <label className="block text-sm font-medium text-slate-700 mb-2">Centro de Costo</label>
            <input
              type="text"
              placeholder="Centro de costo"
              value={centroCosto}
              onChange={(e) => setCentroCosto(e.target.value)}
              className="w-full border border-slate-300 rounded-lg px-3 py-2 focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
            />
          </div>
        </div>
      </div>

      {/* Product Code Entry Section */}
      <div className="bg-white shadow-md rounded-lg p-6">
        <h2 className="text-lg font-semibold mb-4 text-slate-800">3. Agregar Códigos de Productos</h2>
        <div className="grid grid-cols-1 md:grid-cols-4 gap-4 mb-4">
          <div>
            <label className="block text-sm font-medium text-slate-700 mb-2">Código EPP</label>
            <input
              type="text"
              placeholder="Código del producto"
              value={codigoInput}
              onChange={(e) => setCodigoInput(e.target.value)}
              className="w-full border border-slate-300 rounded-lg px-3 py-2 focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
            />
          </div>
          <div>
            <label className="block text-sm font-medium text-slate-700 mb-2">Cantidad</label>
            <input
              type="number"
              placeholder="Cantidad"
              value={cantidadInput}
              onChange={(e) => setCantidadInput(e.target.value)}
              className="w-full border border-slate-300 rounded-lg px-3 py-2 focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
              min="1"
            />
          </div>
          <div>
            <label className="block text-sm font-medium text-slate-700 mb-2">Observación</label>
            <input
              type="text"
              placeholder="Observación (opcional)"
              value={observacionInput}
              onChange={(e) => setObservacionInput(e.target.value)}
              className="w-full border border-slate-300 rounded-lg px-3 py-2 focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
            />
          </div>
          <div className="flex items-end">
            <button
              onClick={handleAgregarCodigo}
              className="w-full bg-orange-500 text-white px-4 py-2 rounded-lg hover:bg-orange-600 transition-colors"
            >
              Agregar Código
            </button>
          </div>
        </div>

        {/* Items List */}
        {consumoItems.length > 0 && (
          <div className="mt-6">
            <h3 className="font-medium text-slate-800 mb-3">Productos Agregados:</h3>
            <div className="overflow-x-auto">
              <table className="min-w-full divide-y divide-slate-200">
                <thead className="bg-slate-50">
                  <tr>
                    <th className="px-4 py-3 text-left text-xs font-medium text-slate-500 uppercase tracking-wider">Código</th>
                    <th className="px-4 py-3 text-left text-xs font-medium text-slate-500 uppercase tracking-wider">Descripción</th>
                    <th className="px-4 py-3 text-left text-xs font-medium text-slate-500 uppercase tracking-wider">Cantidad</th>
                    <th className="px-4 py-3 text-left text-xs font-medium text-slate-500 uppercase tracking-wider">Stock Disponible</th>
                    <th className="px-4 py-3 text-left text-xs font-medium text-slate-500 uppercase tracking-wider">Observación</th>
                    <th className="px-4 py-3 text-left text-xs font-medium text-slate-500 uppercase tracking-wider">Acciones</th>
                  </tr>
                </thead>
                <tbody className="bg-white divide-y divide-slate-200">
                  {consumoItems.map((item, index) => (
                    <tr key={index}>
                      <td className="px-4 py-4 whitespace-nowrap text-sm font-medium text-slate-900">{item.codigo}</td>
                      <td className="px-4 py-4 whitespace-nowrap text-sm text-slate-500">{item.descripcion}</td>
                      <td className="px-4 py-4 whitespace-nowrap text-sm text-slate-900">{item.cantidadConsumida}</td>
                      <td className="px-4 py-4 whitespace-nowrap text-sm text-slate-500">{item.epp?.cantidadActual || 0}</td>
                      <td className="px-4 py-4 whitespace-nowrap text-sm text-slate-500">{item.observacion || '-'}</td>
                      <td className="px-4 py-4 whitespace-nowrap text-sm font-medium">
                        <button
                          onClick={() => handleRemoveItem(index)}
                          className="text-red-600 hover:text-red-900"
                        >
                          Eliminar
                        </button>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>
        )}
      </div>

      {/* Print Preview Modal */}
      {showPrintPreview && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-lg max-w-4xl w-full max-h-[90vh] overflow-y-auto">
            <div className="p-6">
              {/* Header with company image */}
              <div className="text-center mb-6">
                <img 
                  src="https://images.pexels.com/photos/3184465/pexels-photo-3184465.jpeg" 
                  alt="Company Header" 
                  className="w-full h-32 object-cover rounded-lg mb-4"
                />
                <h1 className="text-2xl font-bold text-slate-800">REGISTRO DE CONSUMOS</h1>
                <p className="text-slate-600">Fecha: {new Date().toLocaleDateString()}</p>
              </div>

              {/* Worker Information */}
              {selectedTrabajador && (
                <div className="mb-6">
                  <h2 className="text-lg font-semibold text-slate-800 mb-3">Información del Trabajador</h2>
                  <div className="grid grid-cols-2 gap-4 text-sm">
                    <div><strong>Nombre:</strong> {selectedTrabajador.nombre}</div>
                    <div><strong>RUT:</strong> {selectedTrabajador.rut}</div>
                    <div><strong>Cargo:</strong> {selectedTrabajador.cargo}</div>
                    <div><strong>Email:</strong> {selectedTrabajador.email || 'N/A'}</div>
                  </div>
                </div>
              )}

              {/* Project Information */}
              {selectedProyecto && (
                <div className="mb-6">
                  <h2 className="text-lg font-semibold text-slate-800 mb-3">Información del Proyecto</h2>
                  <div className="grid grid-cols-2 gap-4 text-sm">
                    <div><strong>Proyecto:</strong> {selectedProyecto.nombreProyecto}</div>
                    <div><strong>ID Proyecto:</strong> {selectedProyecto.idProyecto}</div>
                    <div><strong>Administrador:</strong> {selectedProyecto.administrador}</div>
                    <div><strong>Aprobador:</strong> {selectedProyecto.aprobador}</div>
                    {maquina && <div><strong>Máquina:</strong> {maquina}</div>}
                    {centroCosto && <div><strong>Centro de Costo:</strong> {centroCosto}</div>}
                  </div>
                </div>
              )}

              {/* Consumption Items */}
              <div className="mb-6">
                <h2 className="text-lg font-semibold text-slate-800 mb-3">Productos Consumidos</h2>
                <table className="min-w-full divide-y divide-slate-200">
                  <thead className="bg-slate-50">
                    <tr>
                      <th className="px-4 py-3 text-left text-xs font-medium text-slate-500 uppercase">Código</th>
                      <th className="px-4 py-3 text-left text-xs font-medium text-slate-500 uppercase">Descripción</th>
                      <th className="px-4 py-3 text-left text-xs font-medium text-slate-500 uppercase">Cantidad</th>
                      <th className="px-4 py-3 text-left text-xs font-medium text-slate-500 uppercase">Observación</th>
                    </tr>
                  </thead>
                  <tbody className="bg-white divide-y divide-slate-200">
                    {consumoItems.map((item, index) => (
                      <tr key={index}>
                        <td className="px-4 py-4 text-sm font-medium text-slate-900">{item.codigo}</td>
                        <td className="px-4 py-4 text-sm text-slate-500">{item.descripcion}</td>
                        <td className="px-4 py-4 text-sm text-slate-900">{item.cantidadConsumida}</td>
                        <td className="px-4 py-4 text-sm text-slate-500">{item.observacion || '-'}</td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>

              {/* Action Buttons */}
              <div className="flex justify-end space-x-4">
                <button
                  onClick={() => setShowPrintPreview(false)}
                  className="bg-gray-500 text-white px-4 py-2 rounded-lg hover:bg-gray-600 transition-colors"
                >
                  Cancelar
                </button>
                <button
                  onClick={() => window.print()}
                  className="bg-blue-600 text-white px-4 py-2 rounded-lg hover:bg-blue-700 transition-colors"
                >
                  Imprimir
                </button>
                <button
                  onClick={handleConfirmarConsumo}
                  className="bg-green-600 text-white px-4 py-2 rounded-lg hover:bg-green-700 transition-colors"
                >
                  Confirmar Consumo
                </button>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Historical Consumos Table */}
      <div className="bg-white shadow-md rounded-lg p-6">
        <h2 className="text-lg font-semibold mb-4 text-slate-800">Historial de Consumos</h2>
        <div className="overflow-x-auto">
          <table className="min-w-full divide-y divide-gray-200">
            <thead className="bg-gray-50">
              <tr>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Fecha</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Trabajador</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Proyecto</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">EPP</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Cantidad</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Acciones</th>
              </tr>
            </thead>
            <tbody className="bg-white divide-y divide-gray-200">
              {(consumos || []).map((consumo) => (
                <tr key={consumo.id}>
                  <td className="px-6 py-4 whitespace-nowrap">{new Date(consumo.fecha).toLocaleDateString()}</td>
                  <td className="px-6 py-4 whitespace-nowrap">{consumo.trabajador?.nombre || 'N/A'}</td>
                  <td className="px-6 py-4 whitespace-nowrap">{consumo.proyecto?.nombreProyecto || 'N/A'}</td>
                  <td className="px-6 py-4 whitespace-nowrap">{consumo.epp?.codigo || 'N/A'}</td>
                  <td className="px-6 py-4 whitespace-nowrap">{consumo.cantidadConsumida}</td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div className="flex space-x-2">
                      <button
                        onClick={() => window.open(`/api/consumos/${consumo.id}/print`, '_blank')}
                        className="inline-flex items-center px-3 py-1.5 border border-transparent text-xs font-medium rounded-md shadow-sm text-white bg-blue-600 hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 transition-colors"
                        title="Imprimir registro"
                      >
                        <span className="mr-1">🖨️</span>
                        Imprimir
                      </button>
                      <button
                        onClick={() => handleDelete(consumo.id)}
                        className="inline-flex items-center px-3 py-1.5 border border-transparent text-xs font-medium rounded-md shadow-sm text-white bg-red-600 hover:bg-red-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-red-500 transition-colors"
                        title="Eliminar registro"
                      >
                        <span className="mr-1">🗑️</span>
                        Eliminar
                      </button>
                    </div>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
          
          {(consumos || []).length === 0 && (
            <div className="text-center py-8 text-gray-500">
              No hay consumos registrados
            </div>
          )}
        </div>
      </div>

      {/* Print Styles */}
      <style jsx>{`
        @media print {
          .no-print {
            display: none !important;
          }
          body {
            font-size: 12px;
          }
          .print-header {
            page-break-inside: avoid;
          }
        }
      `}</style>
    </div>
  )
}
